<template>
    <div>
        <h1>Komponente in Vue.js</h1>
        <p>
            Schreiben Sie eine Vue.js Single File Component mit einem Text-Eingabefeld und 3 Ausgabefeldern, in denen
            man während des Tippens sehen kann, (a) wie viele Buchstaben (b) wie viele Leerzeichen und (c) wie viele
            Worte man in das Text-Eingabefeld bereits eingegeben hat.
        </p>
        <p>
            Betten Sie Ihre Komponente in eine Webseite zweimal ein und testen Sie, ob beide Komponenten unabhängig
            voneinander sind.
        </p>
        <h2>Template</h2>
        <pre>
            <code>
                {{ example }}
            </code>
        </pre>
        <h2>Script</h2>
        <pre>
            <code>
                &lt;script&gt;
                    export default {
                    data() {
                        return {
                        myText: '',
                        count: 0,
                        spaces: 0,
                        words: 0
                        }
                    },
                    methods: {
                        countings: function () {
                            this.count = this.myText.length;
                            if (this.myText.match(/\S+/g) !== null)
                                this.words = this.myText.match(/\S+/g).length;
                            else
                                this.words = 0;
                            if (this.myText.match(/\s/g) !== null)
                                this.spaces = this.myText.match(/\s/g).length;
                            else
                                this.spaces = 0;
                        },
                    },
                    };
                &lt;/script&gt;
            </code>
        </pre>
    </div>
</template>

<script>
export default{
    data: function() {
        return {
            example: `<template>
                <div id="app">
                    <input v-model="myText" type="TEXTFIELD" @keyup="countings" id="text" />
                    <p>count: {{ count }}</p>
                    <p>spaces: {{ spaces }}</p>
                    <p>words: {{ words }}</p>
                </div>
            </template>`
        }
    }
}
</script>

