<template>
    <div>
        <h1>Menü-Komponente</h1>
        <p>
            Schreiben Sie eine möglichst flexible Vue.js Single File Component für Menüs und wenden Sie diese in derselben Webseite zweimal an, einmal horizontal, das andere Mal vertikal.
        </p>
        <h2>Template</h2>
        <pre>
            <code>
                {{ example }}
            </code>
        </pre>
        <h2>Script</h2>
        <pre>
            <code>
                &lt;script&gt;
                import json from '../assets/subMenus.json';

                export default {
                    name: &quot;MyMenu&quot;,
                    data() {
                        return {
                            item0: &quot;Haus&quot;,
                            item1: &quot;Auto&quot;,
                            item2: &quot;Hund&quot;,
                            horizontal: true,
                            subMenus: json || [],
                            selected: -1
                        }
                    },
                    methods: {
                        itemEnter: function (i) {
                            this.selected = i;
                        },
                        itemLeave: function () {
                            this.selected = -1;
                        },
                    },
                };
                &lt;/script&gt;
            </code>
        </pre>
        <h2>JSON</h2>
        <pre>
            <code>
                [
                    [&quot;Dach&quot;, &quot;Fenster&quot;],
                    [&quot;Reifen&quot;, &quot;Bremse&quot;],
                    [&quot;Futter&quot;, &quot;Spielzeug&quot;]
                ]
            </code>
        </pre>
    </div>
</template>

<script>
export default{
    data: function() {
        return {
            example: `<template>
            <div id="App">
                <div id="menu" :style="horizontal ? { flexDirection: 'row' } : { flexDirection: 'column' }">
                    <div @mouseenter="itemEnter(0)" @mouseleave="itemLeave()">
                        <div class="item">
                            {{ item0 }}
                        </div>
                        <div v-if="selected === 0" id="subMenu">
                            <div class="subMenuItem" v-for="el in subMenus[0]" :key="el">
                                {{ el }}
                            </div>
                        </div>
                    </div>
                    <div @mouseenter="itemEnter(1)" @mouseleave="itemLeave()">
                        <div class="item">
                            {{ item1 }}
                        </div>
                        <div v-if="selected === 1" id="subMenu">
                            <div class="subMenuItem" v-for="el in subMenus[1]" :key="el">
                                {{ el }}
                            </div>
                        </div>
                    </div>
                    <div @mouseenter="itemEnter(2)" @mouseleave="itemLeave()">
                        <div class="item">
                            {{ item2 }}
                        </div>
                        <div v-if="selected === 2" id="subMenu">
                            <div class="subMenuItem" v-for="el in subMenus[2]" :key="el">
                                {{ el }}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            </template>`
        }
    }
}
</script>

<style></style>