<template>
  <table id="CodeExampleTable" border="0" cellspacing="5" cellpadding="5">

    <tr>
      <th>
        Code
      </th>
      <th>
        Output
      </th>
    </tr>
    <tr>
      <td>
        <textarea id="sourceCode" class="box" v-model="js" rows="10">
        </textarea>
      </td>
      <td>
        <iframe name="targetCode" id="targetCode"></iframe>
      </td>
    </tr>
    <tr>
      <td colspan="2">
        <div class="button" @click="executeJS">Try</div>
      </td>
    </tr>
  </table>


</template>
<script>
export default {
  props: ['msg'],
  data: function () {
    return {
      js: this.msg,
      result: '',
    }
  },
  methods: {
    executeJS: function () {
      // Quelle: https://fellowtuts.com/html-css/make-a-html-css-js-try-it-yourself-editor/
      var content = document.getElementById('sourceCode').value;
      var iframe = document.getElementById('targetCode');
      iframe = (iframe.contentWindow) ? iframe.contentWindow : (iframe.contentDocument.document) ? iframe.contentDocument.document : iframe.contentDocument;
      iframe.document.open();
      iframe.document.write(content);
      iframe.document.close();
      return false;
    }
  }
}
</script>
<style>
#CodeExampleTable {
  width: 100;
}

iframe {
  height: 50vw;

}

#sourceCode,
#targetCode {
  border: 2px solid #ddd;
  height: 50vh;
  width: 40vw;
}

.box {
  text-align: left;
}

.button {
  border-left: solid thin white;
  border-top: solid thin white;
  background-color: #35495e;
  color: white;
  text-align: center;
}
</style>