<template>
    <div id="SubMenuHTML">
        <div v-for="(item, index) in items" :key="item" class="item" @click="test(index)">
            {{ item }}
        </div>
    </div>
</template>

<script>
export default {
    name: "SubMenuHTML",
    data: function () {
        return {
            items: [
                'Fachliche Argumentation über Erfolgsprinzipien des WWW',
                'HTTP',
                'HTML-Literatur lesen und Fragen beantworten',
                'HTML Wireframe'
            ],
        }
    },
    methods: {
        test: function (i) {
            this.$emit('sub-menu-clicked', i);
        }
    }
}
</script>

