<template>
    <div id="subMenu">
        <component :is="msg" v-on:sub-menu-clicked="msg => select(msg)"/>
    </div>
</template>

<script>
import HTML from './SubMenuHTML.vue';
import CSS from './SubMenuCSS.vue';
import JavaScript from './SubMenuJavaScript.vue';
import Other from './SubMenuOther.vue';

export default {
    name: "SubMenus",
    props: ['msg'],
    components: {
        HTML,
        CSS,
        JavaScript,
        Other
    },
    data: function() {
        return {
            selected: 'HTML'
        }
    },
    methods: {
        select: function(msg) {
            this.$emit('sub-menu-clicked', msg);
        }
    }
}
</script>

