<template>
    <div id="MainMenu">
        <div v-for="item in menu" :key="item" class="item" @click="select(item)">
            {{ item }}
        </div>
    </div>

</template>

<script>
export default {
    name: "TestComponent",
    data: function () {
        return {
            menu: ['HTML', 'CSS', 'JavaScript', 'Other'],
        }
    },
    methods: {
        select: function(item) {
            this.$emit('menu-clicked', item);
        }
    }
}
</script>

