<template>
    <div>
        <h1>Performanz-Messungen von DOM-Operationen</h1>
        <p>
            Implementieren Sie Performanz-Messungen zum Vergleich von

            innerHTML,
            innerText,
            textContent und
            outerHTML

            selbstständig in JavaScript durch Nutzung der DOM-API. Geben Sie die Messergebnisse als Tabelle aus.
            Verwenden Sie die eingebauten Zeitmessung-Funktionen performance.now(), siehe auch
            When-milliseconds-are-not-enough-performance-now. Suchen Sie eine möglichst kurze und elegante Lösung.
        </p>
        <p>
            Dabei ist zu beachten, dass Browser, um potenzielle Sicherheitsbedrohungen wie Meltdown oder Spectre zu
            minimieren, den zurückgegebenen Wert normalerweise um einen bestimmten Betrag runden. Dies führt zu einer
            gewissen Ungenauigkeit. Beispielsweise rundet Firefox die zurückgegebene Zeit in Schritten von 1
            Millisekunde. Diese Zwangsrundung kann man jedoch wiederum abschalten mittels der Firefox-Einstellung
            privacy.reduceTimerPrecision, siehe How to get microsecond timings in JavaScript since Spectre and Meltdown.
        </p>
        <table border="1">
            <h1>Messdaten</h1>
            <tr>
                <th>innerHTML</th>
                <th>innerText</th>
                <th>textContent</th>
                <th>outerHTML</th>
            </tr>
            <tr>
                <td v-bind:value="innerHTML">{{ innerHTML }}</td>
                <td v-bind:value="innerText">{{ innerText }}</td>
                <td v-bind:value="textContent">{{ textContent }}</td>
                <td v-bind:value="outerHtml">{{ outerHtml }}</td>
            </tr>
        </table>
        <pre>
            <code>
                const testObject = document.createElement('div');
                let t0 = 0;
                let t1 = 0;
                const text = `Lorem ipsum ...`
                let values = [];

                // innerHTML
                t0 = performance.now();
                for (let i = 0; i &#060; 10_000; ++i) {
                    testObject.innerHTML = text;
                }
                t1 = performance.now();
                values.push(t1 - t0);

                // innerText
                t0 = performance.now();
                for (let i = 0; i &#060; 10_000; ++i) {
                    testObject.innerText = text;
                }
                t1 = performance.now();
                values.push(t1 - t0);

                // textContent
                t0 = performance.now();
                for (let i = 0; i &#060; 10_000; ++i) {
                    testObject.textContent = text;
                }
                t1 = performance.now();
                values.push(t1 - t0);

                // outerHTML
                t0 = performance.now();
                for (let i = 0; i &#060; 10_000; ++i) {
                    testObject.outerHTML = text;
                }
                t1 = performance.now();
                values.push(t1 - t0);

                // show 
                ...
            </code>
        </pre>
    </div>
</template>

<script>
export default {
    name: "JavaScript_6",
    data: function () {
        return {
            innerHTML: '',
            innerText: '',
            textContent: '',
            outerHtml: '',
        }
    },
    methods: {
        runTests: function () {
            const testObject = document.createElement('div');
            let t0 = 0;
            let t1 = 0;
            const text = `Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet. Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet.`

            let values = [];

            // innerHTML
            t0 = performance.now();
            for (let i = 0; i < 10_000; ++i) {
                testObject.innerHTML = text;
            }
            t1 = performance.now();
            values.push(t1 - t0);

            // innerText
            t0 = performance.now();
            for (let i = 0; i < 10_000; ++i) {
                testObject.innerText = text;
            }
            t1 = performance.now();
            values.push(t1 - t0);

            // textContent
            t0 = performance.now();
            for (let i = 0; i < 10_000; ++i) {
                testObject.textContent = text;
            }
            t1 = performance.now();
            values.push(t1 - t0);

            // outerHTML
            t0 = performance.now();
            for (let i = 0; i < 10_000; ++i) {
                testObject.outerHTML = text;
            }
            t1 = performance.now();
            values.push(t1 - t0);

            // show 
            this.innerHTML = values[0];
            this.innerText = values[1];
            this.textContent = values[2];
            this.outerHtml = values[3];
        }
    },
    mounted() {
        this.runTests();
    }

}
</script>

